body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-color: #282c34;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 30vmin;
  padding: 30px;
  pointer-events: none;
}

.App-footer {
  min-height: 10vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  font-size: calc(6px + 1vmin);
  color: white;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  
}

.App-animation-color-cycle {
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  -webkit-animation-name: colorcycle;
          animation-name: colorcycle;
  -webkit-animation-duration: 10s;
          animation-duration: 10s;
  text-decoration: none;
}

@-webkit-keyframes colorcycle
{
  0%   {color: #38844B;}
  25%  {color: #1E5ADF;}
  50%  {color: #C63D2D;}
  75%  {color: #F4AA3C;}
  100% {color: #38844B;}
}

@keyframes colorcycle
{
  0%   {color: #38844B;}
  25%  {color: #1E5ADF;}
  50%  {color: #C63D2D;}
  75%  {color: #F4AA3C;}
  100% {color: #38844B;}
}


.NavBar {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    padding: 2vh;
    padding-left: 30%;
    padding-right: 30%;
    -webkit-justify-content: center;
            justify-content: center;
    color: rgb(63, 63, 63);
    min-height: 5vh;
}

.NavBar-link {
    margin: 8px;
    padding: 16px;
    text-decoration: none;
    overflow: visible;
    display: inline-block;
    cursor: pointer;
    border-radius: 360px;
}

.NavBar-link:hover {
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.15);
}

.row {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    width: 100%;
}

.column {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex-basis: 100%;
            flex-basis: 100%;
    -webkit-flex: 1 1;
            flex: 1 1;
    color: white;
    height: auto;
}

.blue-column {
    background-color: blue;
    height: 100px;
}

.green-column {
    background-color: green;
    height: 100px;
}

.NavBar a.NavBar-link:hover, a.NavBar-link:active {
    color: rgb(247, 168, 84);
    text-decoration: none;
}

.NavBar a.NavBar-link:link, a.NavBar-link:visited {
    color: white;
    text-decoration: none;
}
.About {
    padding: 50px;
    min-height: 40vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: baseline;
            justify-content: baseline;
    text-align: justify;
    font-size: 32px;
    color: white;
    letter-spacing: 2px;
  }

.About-text-main {
  font-size: 36px;
  line-height: 1.5em;
}

.About-text-subtitle {
  font-size: 24px;
}

.About-text {
  font-size: 12px;
  line-height: 2em;
  text-transform: uppercase;
}

.InLine {
    display:inline;
  }
  
.About-relocate {
  font-size: 11px;
  line-height: 2em;
  text-transform: uppercase;
  }

.About-link {
    color: #61dafb;
    text-decoration: none;
  }

.profile-picture-about {
  display: none;
  height: 100px;
  width: 100px;
  max-height: 100px;
  max-width: 100px;
  min-height: 100px;
  min-width: 100px;
  border-radius: 360px;
  border: 2px solid rgba(76, 175, 80, 0.6);
  text-decoration: none;
  margin-right: 60px;
  border-radius: 360px;
  display: none
}

/* desktop-medium */
@media all and (min-width: 841px) {    
  .profile-picture-about {
      display: block;
      
      height: 300px;
      width: 300px;
      max-height: 300px;
      max-width: 300px;
      min-height: 300px;
      min-width: 300px;
  
      margin-right: 60px;
  
      border-radius: 360px;
      border: 4px solid rgba(76, 175, 80, 0.6);

      -webkit-animation-duration: 10s;

              animation-duration: 10s;
      -webkit-animation-timing-function: ease;
              animation-timing-function: ease;
      -webkit-animation-delay: 0s;
              animation-delay: 0s;
      -webkit-animation-iteration-count: infinite;
              animation-iteration-count: infinite;
      -webkit-animation-direction: normal;
              animation-direction: normal;
      -webkit-animation-fill-mode: none;
              animation-fill-mode: none;
      -webkit-animation-play-state: running;
              animation-play-state: running;
      -webkit-animation-name: colorcyclepic;
              animation-name: colorcyclepic;
    }
}


@-webkit-keyframes colorcyclepic
{
  0%   {border-color: #38844B;}
  25%  {border-color: #1E5ADF;}
  50%  {border-color: #C63D2D;}
  75%  {border-color: #F4AA3C;}
  100% {border-color:#38844B;}
}


@keyframes colorcyclepic
{
  0%   {border-color: #38844B;}
  25%  {border-color: #1E5ADF;}
  50%  {border-color: #C63D2D;}
  75%  {border-color: #F4AA3C;}
  100% {border-color:#38844B;}
}


